import React from "react";

const Account = () => {
  return (
    <div className="accountcontainer">
      <div className="navdiv">
        <nav className="newnavbar">
          <div className="logo">
             <img src="https://smoothhiring.com/wp-content/uploads/2021/03/Smooth-hiring-logo-001-02.png" width="200" height="40" alt="logo"/>
          </div>
          <div className="already">
            <p>Already have an Account?</p>

            <button>Login</button>
          </div>
        </nav>
      </div>
      
      <div className="freecall"> <i class="fas fa-phone-alt"></i>
      
         <span>Call Toll Free: <span className="spannumber"> 1 (877) 789-8767</span>
        </span>
      </div>
      
      <div className="mins">
        <h1>You’re 5 minutes away from better hiring!</h1>

        <h3>Create your account and post your job in minutes</h3>
      </div>
      <div className="acntcontainer">
        <div className="midcon">
          
          <div className="details">
            <div className='fname'>
              <label for="fname">First Name</label>
              <input
                type="text"
                id="fname"
                name="fname"
                placeholder="Enter your First Name"
                required
              />
            </div>
            <div className="lname">
              <label for="lname">Last Name</label>
              <input
                type="text"
                id="lname"
                name="lname"
                placeholder="Enter your Last Name"
                required
              />
            </div>
            <div className="cname">
              <label for="cname">Company Name</label>
              <input type="text" id="cname" name="cname" required />
            </div>
            <div className="phno">
              <label for="phno">Phone Number</label>
             <div className="phno_ph_no"> <input
                type="phone"
                id="phno"
                name="phno"
                placeholder="(333)"
                required
              />
              <input
                type="phone"
                id="ph.no"
                name="phno"
                placeholder="4444-55555"
                required
              /></div>
            </div>
            <div className="wemail">
              <label for="wemail">Work Email</label>
              <input type="email" id="wemail" name="wemail" required />
            </div>
            <div className="accpwd">
              <label for="password">Password</label>
              <input type="password" id="password" name="password" required />
            </div>
            <div className="cntry">
                <label for="country">Country</label>
                <div className="cntry_dwn"><input type="text" id="country" name="country" placeholder="Select a Country"/><span><i class="fa fa-caret-down" aria-hidden="true"></i></span></div>
            </div>
            <div className="indstry">
                <label for="industry">Industry</label>
                <div className="indstry_dwn"><input type="text" id="industry" name="industry" placeholder="Select an Industry" /><span><i class="fa fa-caret-down" aria-hidden="true"></i></span></div>
            </div>
            <div className="csize">
                <label for="csize">Company Size</label>
                <div className="csize_dwn"><input type="text" id="csize" name="csize" placeholder="Select number of Employees" /><span><i class="fa fa-caret-down" aria-hidden="true"></i></span></div>
            </div>
            <div className="Huknowus">
              <label for="howuknow">How did you hear about us?</label>
              <input
                type="text"
                id="howuknow"
                name="howuknow"
                placeholder="Optional(but we'd love to Know!)"
                required
              />
            </div>
            
          </div>
          <div className="knowus">
            <div>
              <h2>Why SmoothHiring?</h2>
            </div>
            <div>
              <p>
                <span className="green">Save Time:</span> Our platform automates
                job postings to all job boards thus reaching every job seeker on
                all job boards.
              </p>
            </div>
            <div>
              <p>
                <span className="green">Better Value:</span> recruit from
                multiple job boards at the push of a button.
              </p>
            </div>
            <div>
              <p>
                <span className="green">Better Results:</span> Patended
                technology Predicts Employee success 5X better than Traditional
                Hiring.
              </p>
            </div>
          </div>
        </div>
        <div className="cntnubtn">
              <button className="continuebtn">Continue</button>
              <div className="Vpp"><a href="#">We value your privacy</a></div>
            </div>
      </div>
      <div className="Footer">© 2010 - 2021 ClearFit</div>
    </div>
  );
};


export default Account;